module.exports = "import { Component, ElementRef, ViewChild } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-tag-control',\n  template: `\n    <nz-tag\n      *ngFor=\"let tag of tags; let i = index;\"\n      [nzMode]=\"i === 0 ? 'default' : 'closeable'\"\n      (nzAfterClose)=\"handleClose(tag)\">\n      {{ sliceTagName(tag) }}\n    </nz-tag>\n    <nz-tag\n      *ngIf=\"!inputVisible\"\n      class=\"editable-tag\"\n      (click)=\"showInput()\">\n      <i nz-icon type=\"plus\"></i> New Tag\n    </nz-tag>\n    <input\n      #inputElement\n      nz-input\n      nzSize=\"small\"\n      *ngIf=\"inputVisible\" type=\"text\"\n      [(ngModel)]=\"inputValue\"\n      style=\"width: 78px;\"\n      (blur)=\"handleInputConfirm()\"\n      (keydown.enter)=\"handleInputConfirm()\">\n  `,\n  styles  : [\n      `.editable-tag ::ng-deep .ant-tag {\n      background: rgb(255, 255, 255);\n      border-style: dashed;\n    }`\n  ]\n})\nexport class NzDemoTagControlComponent {\n\n  tags = [ 'Unremovable', 'Tag 2', 'Tag 3' ];\n  inputVisible = false;\n  inputValue = '';\n  @ViewChild('inputElement') inputElement: ElementRef;\n\n  handleClose(removedTag: {}): void {\n    this.tags = this.tags.filter(tag => tag !== removedTag);\n  }\n\n  sliceTagName(tag: string): string {\n    const isLongTag = tag.length > 20;\n    return isLongTag ? `${tag.slice(0, 20)}...` : tag;\n  }\n\n  showInput(): void {\n    this.inputVisible = true;\n    setTimeout(() => {\n      this.inputElement.nativeElement.focus();\n    }, 10);\n  }\n\n  handleInputConfirm(): void {\n    if (this.inputValue && this.tags.indexOf(this.inputValue) === -1) {\n      this.tags.push(this.inputValue);\n    }\n    this.inputValue = '';\n    this.inputVisible = false;\n  }\n}\n"